body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__image {
  margin: 10px 0;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__item {
  text-transform: uppercase;
  position: relative;
}

.header__item:not(:first-of-type) {
  margin-left: 20px;
}

.header__link {
  color: #060b35;
  margin: 22px 0 23px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.header__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  padding: 22.5px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: block;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin-top: 19px;
  display: block;
  position: absolute;
}
